import React from 'react';
import classes from './DrawerToggle.module.css';

const drawerToggle = (props) => (
    <div onClick={props.onclick} className={classes.DrawerToggle}>
      <div></div>
      <div></div>
      <div></div>
    </div>
  );
export default drawerToggle;



